.calendar-container {
  position: relative;
  width: 100vw;           /* Viewport width */
  height: auto;
  max-width: 100%;
  margin: 0 auto;
}

.calendar-background {
  width: 100%;
  height: auto;
  display: block;
}

.day-region {
  position: absolute;
  background: rgba(255, 255, 255, 0); /* fully transparent */
  border: none;
  cursor: pointer;
}

.day-region:hover {
  outline: 2px dashed rgba(255, 255, 255, 0.3); /* for debug or hover effect */
}

